.helvetica-700 {
  font-family: "Helvetica-Bold", "Arial", sans-serif;
  font-weight: 700;
}
.helvetica {
  font-family: "Helvetica", "Arial", sans-serif;
  font-weight: 400;
}
.reset-btn {
  padding: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.hover {
  transition: color 0.3s;
}
.hover:hover {
  color: #000000;
  transition: color 0.1s;
}
.align-center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.align-top {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}
.align-left {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}
@font-face {
  font-family: "Helvetica";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/Helvetica.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Helvetica-Bold";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/Helvetica-Bold.woff2") format("woff2");
  font-display: swap;
}
body {
  margin: 0;
}
main {
  flex-grow: 1;
}
img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
a {
  color: inherit;
  text-decoration: none;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.breadcrumbs {
  padding: 2.56rem 0 0;
}
.breadcrumbs__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}
.breadcrumbs__item + .breadcrumbs__item {
  margin-left: 0.625rem;
}
.breadcrumbs__item:first-of-type {
  color: #9F9F9F;
}
.breadcrumbs__item:first-of-type::after {
  content: '/';
  margin-left: 0.625rem;
}
@media (min-width: 1920px) {
  .breadcrumbs {
    padding: 3.125rem 0 1rem;
  }
}
.cart__group {
  margin: 0;
  padding: 0;
  border: none;
}
.cart__text {
  margin: 0;
  padding: 0.75rem 0 2rem;
}
.cart__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.cart__link {
  color: #9F9F9F;
  font-size: 1rem;
  line-height: calc(18/16);
  text-decoration: underline;
  transition: color 0.3s;
}
.cart__link:hover {
  color: #000000;
  transition: color 0.1s;
}
.cart__item {
  position: relative;
}
.cart__item + .cart__item {
  margin-top: 1rem;
}
.cart__input {
  width: 100%;
  min-height: 43px;
  font-size: 1rem;
  line-height: calc(19/16);
  resize: none;
}
.cart__input + .cart__label {
  position: absolute;
  top: 50%;
  left: 0;
  transform: translate(0, -50%);
  color: #9F9F9F;
}
.cart__input:focus + .cart__label,
.cart__input:not(:focus):valid + .cart__label {
  transform: translate(0, -40px) scale(0.9);
  transition: transform 0.2s, scale 0.2s;
}
.burger {
  top: 1rem;
  left: 1rem;
  z-index: 10;
  display: block;
  width: 28px;
  height: 20px;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
  border: none;
}
.burger__inner,
.burger__inner::before,
.burger__inner::after {
  display: block;
  position: absolute;
  width: 28px;
  height: 2px;
  background-color: #000000;
}
.burger__inner::before {
  content: "";
  top: -10px;
}
.burger__inner::after {
  content: "";
  bottom: -10px;
}
.button {
  padding: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
  min-width: 288px;
  padding: 19px;
  font-family: "Helvetica-Bold", "Arial", sans-serif;
  font-weight: 700;
  font-size: 1.25rem;
  line-height: calc(23/20);
  border: 1px solid #000000;
}
.container {
  padding: 1rem 1rem;
}
@media (min-width: 1920px) {
  .container {
    padding: 2.1rem 25.3rem;
  }
}
h1 {
  margin: 0;
  font-family: "Helvetica-Bold", "Arial", sans-serif;
  font-weight: 700;
  font-size: 1.875rem;
  line-height: calc(34/30);
}
@media (min-width: 1920px) {
  h1 {
    font-size: 2.5rem;
    line-height: calc(46/40);
  }
}
.input {
  min-width: 250px;
  padding: 0.375rem 0;
  border: none;
  border-bottom: 1px solid #9F9F9F;
  border-radius: 0;
  outline: none;
}
.input::placeholder {
  font-family: "Helvetica", "Arial", sans-serif;
  font-weight: 400;
  color: #9F9F9F;
}
.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__label {
  display: block;
  width: 22px;
  height: 22px;
  margin-left: 1rem;
  mask-image: url('../images/svg-sprites/sprite.svg#search');
  mask-repeat: no-repeat;
  mask-size: cover;
  mask-position: center;
  background-color: #000000;
}
.header__input {
  display: none;
}
.header__link {
  display: block;
  mask-repeat: no-repeat;
  mask-size: cover;
  mask-position: center;
  background-color: #000000;
}
.header__link--logo {
  width: 77px;
  height: 24px;
  margin: 0 auto;
  mask-image: url('../images/svg-sprites/sprite.svg#logo');
}
.header__link--login {
  display: none;
  width: 25px;
  height: 27px;
  mask-image: url('../images/svg-sprites/sprite.svg#login');
}
.header__link--favourite {
  width: 25px;
  height: 22px;
  mask-image: url('../images/svg-sprites/sprite.svg#favourite');
}
.header__link--cart {
  position: relative;
  width: 22px;
  height: 24px;
  mask-image: url('../images/svg-sprites/sprite.svg#cart');
}
.header__link--cart::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 1rem;
  height: 1rem;
  background-color: #000000;
  border-radius: 50%;
}
.header__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}
.header__item {
  position: relative;
}
.header__item + .header__item {
  margin-left: 1rem;
}
.header__count {
  position: absolute;
  top: -18%;
  right: -15%;
  color: #FCFCFC;
  z-index: 1;
  font-size: 0.75rem;
  line-height: calc(14/12);
}
.header__count::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 1rem;
  height: 1rem;
  background-color: #000000;
  border-radius: 50%;
  z-index: -1;
}
@media (min-width: 1920px) {
  .header {
    position: relative;
  }
  .header__label {
    width: 25px;
    height: 25px;
  }
  .header__input {
    display: block;
    margin-left: 1.3rem;
    margin-right: auto;
    font-size: 1.125rem;
    line-height: calc(20/18);
  }
  .header__input::placeholder {
    font-size: 1.125rem;
    line-height: calc(20/18);
  }
  .header__link--logo {
    position: absolute;
    top: 38%;
    left: 50%;
    transform: translate(-50%, 0);
    width: 160px;
    height: 50px;
  }
  .header__link--login {
    display: block;
  }
  .header__link--favourite {
    width: 28px;
    height: 25px;
  }
  .header__link--cart {
    width: 25px;
    height: 26px;
  }
}
.nav {
  display: none;
}
@media (min-width: 1920px) {
  .nav {
    display: flex;
    justify-content: space-between;
    padding: 4.3rem 0 0;
  }
  .nav__list {
    display: flex;
    margin: 0;
    padding: 0;
    list-style: none;
  }
  .nav__list:first-of-type {
    counter-reset: section;
  }
  .nav__list:first-of-type .nav__link::after {
    content: " " counter(section);
    counter-increment: section;
  }
  .nav__list:last-of-type {
    counter-reset: section 3;
  }
  .nav__list:last-of-type .nav__link::after {
    content: " " counter(section);
    counter-increment: section;
  }
  .nav__item + .nav__item {
    margin-left: 1.875rem;
  }
  .nav__link {
    font-family: "Helvetica-Bold", "Arial", sans-serif;
    font-weight: 700;
    font-size: 1.125rem;
    line-height: calc(20/18);
    color: #9F9F9F;
    text-transform: uppercase;
    transition: color 0.3s;
  }
  .nav__link:hover {
    color: #000000;
    transition: color 0.1s;
  }
}
.page {
  height: 100%;
}
.page__body {
  min-width: 320px;
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  font-family: "Helvetica", "Arial", sans-serif;
  font-weight: 400;
  font-size: 1rem;
  line-height: calc(19/16);
}
