.cart__group {
  margin: 0;
  padding: 0;
  border: none;
}

.cart__text {
  margin: 0;
  padding: 0.75rem 0 2rem;
}

.cart__list {
  .reset-list;
}

.cart__link {
  color: @grey-1;
  .font(1rem, calc(18/16));
  text-decoration: underline;
  .hover;
}

.cart__item {
  position: relative;
  & + & {
    margin-top: 1rem;
  }
}

.cart__input {
  width: 100%;
  min-height: 43px;
  .font(1rem, calc(19/16));
  resize: none;
}

.cart__input + .cart__label {
  position: absolute;
  top: 50%;
  left: 0;
  transform: translate(0, -50%);
  color: @grey-1;
}

// Анимация label у input
.cart__input:focus + .cart__label,
.cart__input:not(:focus):valid + .cart__label {
  transform: translate(0, -40px) scale(0.9);
  transition: transform 0.2s, scale 0.2s;
}
